"use client"

import { useAuth } from "@/context/auth-context"
import { useRouter, usePathname } from "next/navigation"
import { useState, useEffect } from "react"
import {
  MdDashboard,
  MdLocalGasStation,
  MdWork,
  MdPayments,
  MdNotifications,
  MdPerson,
  MdMenu,
  MdClose,
  MdLogout,
  MdDarkMode,
  MdLightMode,
} from "react-icons/md"
import Link from "next/link"

export default function AttendantLayout({ children }: { children: React.ReactNode }) {
  const { user, isAuthenticated, logout } = useAuth()
  const router = useRouter()
  const pathname = usePathname()

  const [sidebarOpen, setSidebarOpen] = useState(true)
  const [theme, setTheme] = useState<"light" | "dark">("light")

  useEffect(() => {
    if (!isAuthenticated || user?.role !== "ATTENDANT") {
      router.push("/login")
    }
  }, [isAuthenticated, user, router])

  // Load theme from localStorage
  useEffect(() => {
    const storedTheme = localStorage.getItem("theme") as "light" | "dark"
    if (storedTheme) {
      setTheme(storedTheme)
      document.documentElement.classList.toggle("dark", storedTheme === "dark")
    }
  }, [])

  // Apply theme changes
  const toggleTheme = () => {
    const newTheme = theme === "light" ? "dark" : "light"
    setTheme(newTheme)
    localStorage.setItem("theme", newTheme)
    document.documentElement.classList.toggle("dark", newTheme === "dark")
  }

  const toggleSidebar = () => setSidebarOpen((prev) => !prev)

  return (
    <div className={`flex min-h-screen transition-colors duration-300 ${theme === "dark" ? "bg-gray-900 text-gray-100" : "bg-gray-100 text-gray-900"}`}>
      {/* Sidebar */}
      <aside
        className={`fixed lg:relative z-40 top-0 left-0 h-full flex flex-col p-4 transition-all duration-300 ease-in-out
          ${sidebarOpen ? "w-64" : "w-20"}
          ${theme === "dark" ? "bg-[#102525]" : "bg-[#1a3a3a] text-white"}`}
      >
        {/* Sidebar header */}
        <div className="flex items-center justify-between mb-6">
          <h1
            className={`text-2xl font-bold transition-opacity duration-300 ${
              sidebarOpen ? "opacity-100" : "opacity-0 hidden"
            }`}
          >
            â›½ Attendant
          </h1>
          <button
            onClick={toggleSidebar}
            className="text-2xl hover:text-yellow-400"
            aria-label="Toggle sidebar"
          >
            {sidebarOpen ? <MdClose /> : <MdMenu />}
          </button>
        </div>

        {/* Navigation */}
        <nav className="flex flex-col space-y-3">
          <NavLink href="/attendant/dashboard" icon={<MdDashboard />} label="Dashboard" active={pathname === "/attendant/dashboard"} sidebarOpen={sidebarOpen} />
          <NavLink href="/attendant/sales" icon={<MdLocalGasStation />} label="Fuel Sales" active={pathname === "/attendant/sales"} sidebarOpen={sidebarOpen} />
          <NavLink href="/attendant/shifts" icon={<MdWork />} label="Shifts" active={pathname === "/attendant/shifts"} sidebarOpen={sidebarOpen} />
          <NavLink href="/attendant/payments" icon={<MdPayments />} label="Payments" active={pathname === "/attendant/payments"} sidebarOpen={sidebarOpen} />
          <NavLink href="/attendant/alerts" icon={<MdNotifications />} label="Alerts" active={pathname === "/attendant/alerts"} sidebarOpen={sidebarOpen} />
          <NavLink href="/attendant/profile" icon={<MdPerson />} label="Profile" active={pathname === "/attendant/profile"} sidebarOpen={sidebarOpen} />
        </nav>

        {/* Logout Button */}
        <button
          onClick={logout}
          className={`mt-auto flex items-center gap-3 bg-red-600 hover:bg-red-700 font-semibold py-2 rounded-lg transition-all ${
            sidebarOpen ? "justify-center" : "justify-center w-10 h-10 mx-auto"
          }`}
        >
          <MdLogout />
          {sidebarOpen && <span>Logout</span>}
        </button>
      </aside>

      {/* Main Section */}
      <div
        className={`flex-1 flex flex-col transition-all duration-300 ${
          sidebarOpen ? "lg:ml-64" : "lg:ml-20"
        }`}
      >
        {/* Top Bar */}
        <header className={`shadow-sm p-4 flex justify-between items-center sticky top-0 z-20 transition-colors duration-300 ${theme === "dark" ? "bg-gray-800 text-gray-100" : "bg-white text-gray-900"}`}>
          <div className="flex items-center gap-3">
            <button
              className="text-2xl hidden lg:block"
              onClick={toggleSidebar}
              aria-label="Toggle sidebar"
            >
              <MdMenu />
            </button>
            <h2 className="text-lg font-semibold">Welcome, {user?.name}</h2>
          </div>

          {/* Right side actions */}
          <div className="flex items-center gap-4">
            <span className="text-sm text-gray-600 dark:text-gray-300">Role: {user?.role}</span>
            <button
              onClick={toggleTheme}
              className="text-2xl hover:text-yellow-400 transition"
              aria-label="Toggle theme"
            >
              {theme === "light" ? <MdDarkMode /> : <MdLightMode />}
            </button>
          </div>
        </header>

        {/* Page Content */}
        <main className="flex-1 p-6 w-full overflow-y-auto">{children}</main>
      </div>
    </div>
  )
}

function NavLink({
  href,
  icon,
  label,
  active,
  sidebarOpen,
}: {
  href: string
  icon: React.ReactNode
  label: string
  active: boolean
  sidebarOpen: boolean
}) {
  return (
    <Link
      href={href}
      className={`flex items-center gap-3 p-2 rounded-lg transition-all duration-200 
        ${active ? "bg-teal-600 text-white" : "hover:bg-teal-700 text-gray-200"}
        ${sidebarOpen ? "justify-start" : "justify-center"}`}
    >
      <span className="text-xl">{icon}</span>
      {sidebarOpen && <span className="font-medium">{label}</span>}
    </Link>
  )
}
